import { useEffect, useState } from 'react'
import { useAuth } from '../utils/auth'
import { Layout } from '../components/Layout'
import axios from 'axios'


type Activity = {
    id: number
    user_id: number
    action: 'login' | 'logout' | 'search'
    timestamp: string
    metadata?: any
    user?: { first_name: string, last_name: string, email: string }
}


export default function Dashboard() {
    const { token } = useAuth()
    const [activities, setActivities] = useState<Activity[]>([])
    const [loading, setLoading] = useState(false)
    const [page, setPage] = useState(1)
    const [limit, setLimit] = useState(20)
    const [filters, setFilters] = useState({ startDate: '', endDate: '', name: '', email: '', actions: [] as string[] })


    useEffect(() => {
        fetchActivities()
    }, [page, limit])


    async function fetchActivities() {
        setLoading(true)
        try {
            const params: any = { page, limit }
            if (filters.startDate) params.startDate = filters.startDate
            if (filters.endDate) params.endDate = filters.endDate
            if (filters.name) params.name = filters.name
            if (filters.email) params.email = filters.email
            if (filters.actions.length) params.actions = filters.actions
            const res = await axios.get(`${process.env.NEXT_PUBLIC_API_URL || 'http://localhost:4000/api'}/activities`, {
                headers: { Authorization: `Bearer ${token}` },
                params,
            })
            setActivities(res.data.items || res.data)
        } catch (err) {
            console.error(err)
        } finally { setLoading(false) }
    }


    const handleSearch = (e: React.FormEvent) => {
        e.preventDefault()
        setPage(1)
        fetchActivities()
    }
}